import React, { useEffect } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

import "./welcome.styles.scss";
import model from "../../../assets/model.png";

import { TextCircle, useWindowEvent } from "../../../tools/tools";
import LogoComp from "../../logo/logo.component";
import { setRouteChange } from "../../../redux/etc/etc.reducer";

const WelcomeComp = () => {
  const history = useHistory();
  const { routeChange } = useSelector((selector) => selector.etc);
  const dispatch = useDispatch();

  useEffect(() => {
    document.querySelector("body").style.overflow = "hidden";
  }, []);

  useWindowEvent("wheel", (e) => {
    if (Math.sign(e.deltaY) === 1) {
      if (routeChange) return;
      dispatch(setRouteChange(true));
      setTimeout(() => {
        history.push("/services");
        dispatch(setRouteChange(false));
      }, 2000);
    }
  });

  return (
    <div
      className={routeChange ? "welcome-sub-comp active" : "welcome-sub-comp "}
    >
      <div className="filter"></div>
      <div className="number">01</div>
      <LogoComp />
      <div className="text-layer">
        <p className="title">full stack developer</p>
        <p className="sub-title">
          Best tools to ensure the success of your project. Bring your project
          across platforms. Creativity's our language.
        </p>
      </div>
      <button
        className="more-wrapper"
        onClick={() => {
          history.push("/services");
        }}
      >
        <div className="more">
          <p>more</p>
          <div className="scroll-icon"></div>
        </div>
      </button>
      <div className="img-layer">
        <TextCircle words="design front-end back-end wordpress seo react " />
        <img src={model} alt="model" className="model" />
      </div>
      <div className="social-contact">
        <a
          href="https://twitter.com/AlexCamarena95"
          target="_blank"
          rel="noopener noreferrer"
        >
          <FontAwesomeIcon icon={["fab", "twitter"]} />
        </a>
        <a
          href="https://codepen.io/AlexCamarena"
          target="_blank"
          rel="noopener noreferrer"
        >
          <FontAwesomeIcon icon={["fab", "codepen"]} />
        </a>
        <a
          href="https://www.freelancer.com/u/EricHaller"
          target="_blank"
          rel="noopener noreferrer"
        >
          <FontAwesomeIcon icon="handshake" />
        </a>
        <p>follow</p>
      </div>
    </div>
  );
};

export default WelcomeComp;
