import React from "react";
import { useHistory } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";

import "./projects.styles.scss";
import doge from "../../../assets/doge.png";

import { useWindowEvent } from "../../../tools/tools";
import { setRouteChange } from "../../../redux/etc/etc.reducer";

const ProjectsComp = () => {
  const history = useHistory();
  const { routeChange } = useSelector((selector) => selector.etc);
  const dispatch = useDispatch();

  useWindowEvent("wheel", (e) => {
    if (Math.sign(e.deltaY) === -1 && window.scrollY === 0) {
      if (routeChange) return;
      dispatch(setRouteChange(true));
      setTimeout(() => {
        history.push("/services");
        dispatch(setRouteChange(false));
      }, 2000);
    } else if (
      Math.sign(e.deltaY) === 1 &&
      window.innerHeight + window.scrollY >= document.body.offsetHeight
    ) {
      if (routeChange) return;
      dispatch(setRouteChange(true));
      setTimeout(() => {
        history.push("/about");
        dispatch(setRouteChange(false));
      }, 2000);
    }
  });

  return (
    <div className="projects-sub-comp">
      <div className="background"></div>
      <div className="main">
        <img src={doge} alt="doge" />
        <p>More projects coming soon!</p>
      </div>
    </div>
  );
};

export default ProjectsComp;
