import { createSlice } from "@reduxjs/toolkit";

//Get index of the first bar after 'http://'
const strBeforeDomain = window.location.href.indexOf("/", 7);
//Get whatever it be after first bar (page loc)
const pageLoc = window.location.href.slice(strBeforeDomain + 1);

let initState = {
  lastRoute: pageLoc !== "" ? pageLoc : "/",
  toggleMenu: false,
  currentSec: pageLoc !== "" ? pageLoc : "/",
};

const menuSlice = createSlice({
  name: "menu",
  initialState: initState,
  reducers: {
    showMenu(state, action) {
      state.toggleMenu = !state.toggleMenu;
    },
    selectSection(state, action) {
      state.lastRoute = state.currentSec;
      state.currentSec = action.payload;
    },
  },
});

export const { showMenu, selectSection } = menuSlice.actions;

export default menuSlice.reducer;
