import React, { useEffect, useState } from "react";

import { TweenMax } from "gsap";
import classNames from "classnames";

export const TextCircle = ({ words }) => {
  return (
    <svg viewBox="0 0 100 100">
      <path id="circle" d="M9,50a40,40 0 1,0 80,0a40,40 0 1,0 -80,0">
        <animateTransform
          attributeName="transform"
          begin="0s"
          dur="30s"
          type="rotate"
          from="0 50 50"
          to="360 50 50"
          repeatCount="indefinite"
        />
      </path>
      <text dy="5">
        <textPath xlinkHref="#circle">{words}</textPath>
      </text>
    </svg>
  );
};

export function hamburger(mainClass, animate) {
  const element = document.querySelector(`.${mainClass}`);
  element.classList.toggle(`${animate}`);
}

export const useWindowEvent = (event, callback) => {
  useEffect(() => {
    window.addEventListener(event, callback);
    return () => window.removeEventListener(event, callback);
  }, [event, callback]);
};

export const CreateCursor = ({ classN }) => {
  const [position, setPosition] = useState({ x: 0, y: 0 });
  const [clicked, setClicked] = useState(false);
  const [hidden, setHidden] = useState(false);

  useWindowEvent("mousemove", (e) =>
    setPosition({ x: e.clientX, y: e.clientY })
  );

  useWindowEvent("mouseleave", (e) => setHidden(true));

  useWindowEvent("mouseenter", (e) => setHidden(false));

  useWindowEvent("mousedown", (e) => setClicked(true));

  useWindowEvent("mouseup", (e) => setClicked(false));

  const cursorClasses = classNames(classN, {
    clicked: clicked,
    hidden: hidden,
  });

  return (
    <div
      className={cursorClasses}
      style={{
        left: `${position.x}px`,
        top: `${position.y}px`,
      }}
    />
  );
};

export const CreateParallax = (event, resistance, parent, comp) => {
  let rotateX = (event.clientX - parent.offsetWidth / 2) / resistance;
  let rotateY = (event.clientY - parent.offsetHeight / 2) / resistance;

  TweenMax.to(comp, 0.3, {
    transform: `rotateX(${rotateY}deg) rotateY(${rotateX}deg)`,
  });
};

export function useHandleScroll(event, current) {}
