import React, { Component } from "react";
import * as THREE from "three";

class ThreeScene extends Component {
  componentDidMount() {
    this.width = this.mount.clientWidth;
    this.height = this.mount.clientHeight;
    this.loader = new THREE.TextureLoader();

    this.shaders = {
      vertex: `
        varying vec2 vUv;
    
        void main(){
          vUv = uv;
          gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.);
        }
        `,
      fragment: `
        #define PI 3.14159265359
        #define PI2 6.28318530718
        #define S(a,b,n) smoothstep(a,b,n)
    
        uniform float u_time;
        
        uniform vec2 u_resolution;    
        uniform vec2 u_mouse;
    
        uniform sampler2D u_text0;
    
        varying vec2 vUv;
        
        //https://github.com/Jam3/glsl-fast-gaussian-blur
        vec4 blur13(sampler2D image, vec2 uv, vec2 resolution, vec2 direction) {
          vec4 color = vec4(0.0);
          vec2 off1 = vec2(1.411764705882353) * direction;
          vec2 off2 = vec2(3.2941176470588234) * direction;
          vec2 off3 = vec2(5.176470588235294) * direction;
          color += texture2D(image, uv) * 0.1964825501511404;
          color += texture2D(image, uv + (off1 / resolution)) * 0.2969069646728344;
          color += texture2D(image, uv - (off1 / resolution)) * 0.2969069646728344;
          color += texture2D(image, uv + (off2 / resolution)) * 0.09447039785044732;
          color += texture2D(image, uv - (off2 / resolution)) * 0.09447039785044732;
          color += texture2D(image, uv + (off3 / resolution)) * 0.010381362401148057;
          color += texture2D(image, uv - (off3 / resolution)) * 0.010381362401148057;
          return color;
        }
    
        void main(){
          vec2 uv = vUv;
          vec2 st = gl_FragCoord.xy / u_resolution;
          vec2 m = u_mouse;
          
          st.x *= u_resolution.x / u_resolution.y;
          m.x *= u_resolution.x / u_resolution.y;
            
          vec2 m_rel = vec2(m.x,u_resolution.y - m.y) / u_resolution;         
          float d = distance(st, m_rel);
        
          float e = S(.3, .01, d);
    
          vec2 pst = vec2(abs(uv * 2. - 1.));
    
          float x = pst.y * 20. + u_time * 2.;
          float y = pst.x * 20. + u_time * .5;
        
          float posx = cos(x+y) * .5 * cos(y); 
          float posy = sin(x-y) * .5 * cos(y);
    
          vec2 dist = vec2(uv.x + posx * e / 50., uv.y + posy * e / 50.);
          
          vec4 blur = blur13(u_text0, dist, u_resolution, vec2(1., 1.));
    
          vec4 color = texture2D(u_text0, dist);
        
          gl_FragColor = mix(color, blur, e);
        }
        `,
    };
    this.uniforms = {
      u_time: { type: "f", value: 0 },
      u_resolution: {
        type: "v2",
        value: new THREE.Vector2(this.width, this.height),
      },
      u_mouse: { type: "v2", value: new THREE.Vector2(0, 0) },
      u_text0: {
        type: "t",
        value: this.loader.load(
          "https://images.pexels.com/photos/4456224/pexels-photo-4456224.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"
        ),
      },
    };

    this.scene = new THREE.Scene();

    //Add Renderer
    this.renderer = new THREE.WebGLRenderer({ antialias: true });
    this.renderer.setSize(this.width, this.height);
    this.mount.appendChild(this.renderer.domElement);

    this.camera = new THREE.PerspectiveCamera(
      75,
      this.width / this.height,
      0.1,
      1000
    );
    this.camera.position.set(0, 0, 2);

    this.mouse = {
      x: 0,
      y: 0,
    };

    this.addMesh();

    this.renderScene();
    this.start();

    this.onResize();
  }

  addMesh() {
    // -----Step 2--------
    //LOAD TEXTURE and on completion apply it

    const geometry = new THREE.PlaneBufferGeometry(3, 2, 60, 60);
    const material = new THREE.ShaderMaterial({
      uniforms: this.uniforms,
      vertexShader: this.shaders.vertex,
      fragmentShader: this.shaders.fragment,
    });

    this.mesh = new THREE.Mesh(geometry, material);

    this.begin = performance.now();

    this.scene.add(this.mesh);
  }

  lerp(a, b, n) {
    return n * (b - a) + a;
  }

  onResize() {
    const w = window.innerWidth;
    const h = window.innerHeight;

    this.uniforms.u_resolution.value.x = w;
    this.uniforms.u_resolution.value.y = h;

    this.renderer.setSize(w, h);
    this.camera.aspect = w / h;

    if (w / h > 1) {
      this.mesh.scale.x = this.mesh.scale.y = (1.05 * w) / h;
    }

    this.camera.updateProjectionMatrix();
  }

  resizeRendererToDisplaySize = (renderer) => {
    const canvas = renderer.domElement;
    const width = canvas.clientWidth;
    const height = canvas.clientHeight;
    const needResize = canvas.width !== width || canvas.height !== height;
    if (needResize) {
      renderer.setSize(width, height, false);
    }
    return needResize;
  };

  componentWillUnmount() {
    this.stop();
    this.mount.removeChild(this.renderer.domElement);
  }

  start = () => {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.animate);
    }
  };

  stop = () => {
    cancelAnimationFrame(this.frameId);
  };

  animate = () => {
    // -----Step 3--------

    this.uniforms.u_mouse.value.x = this.lerp(
      this.uniforms.u_mouse.value.x,
      this.mouse.x,
      0.05
    );

    this.uniforms.u_mouse.value.y = this.lerp(
      this.uniforms.u_mouse.value.y,
      this.mouse.y,
      0.05
    );

    if (this.resizeRendererToDisplaySize(this.renderer)) {
      const canvas = this.renderer.domElement;
      this.camera.aspect = canvas.clientWidth / canvas.clientHeight;
      this.camera.updateProjectionMatrix();
    }

    this.renderScene();
    this.frameId = window.requestAnimationFrame(this.animate);
  };

  renderScene = () => {
    if (this.renderer) this.renderer.render(this.scene, this.camera);
  };

  onMouseMove = ({ clientX, clientY }) =>
    (this.mouse = {
      x: clientX,
      y: clientY,
    });

  render() {
    return (
      <div
        style={{ width: "100vw", height: "100vh", position: "absolute" }}
        onMouseMove={this.onMouseMove}
        ref={(mount) => {
          this.mount = mount;
        }}
      />
    );
  }
}

export default ThreeScene;
