import React from "react";
import { useDispatch, useSelector } from "react-redux";

import { showMenu } from "../../redux/menu/menu.reducer";
import { setRouteChange } from "../../redux/etc/etc.reducer";
import { hamburger } from "../../tools/tools";
import DelayLink from "../delayLink/delay.component";

import "./aside.styles.scss";

const Aside = ({ sections }) => {
  const dispatch = useDispatch();
  const { currentSec, toggleMenu } = useSelector((selector) => selector.menu);
  const { routeChange } = useSelector((selector) => selector.etc);

  return (
    <div className={toggleMenu ? "aside-wrapper active" : "aside-wrapper"}>
      <div className="opacity-layer"></div>
      <div className="aside">
        <button
          className="bars"
          onClick={() => {
            hamburger("bars", "animate");
            dispatch(showMenu());
          }}
        >
          <span></span>
          <span></span>
          <span></span>
        </button>
        <ul className="menu-content">
          {sections.map((section, index) => (
            <li
              className={currentSec === section.path ? "current" : ""}
              key={index}
            >
              <DelayLink
                to={section.path}
                delay={1500}
                onDelayStart={() => {
                  if (routeChange) return;
                  dispatch(setRouteChange(true));
                }}
                onDelayEnd={() => dispatch(setRouteChange(false))}
              >
                <p data-letters={section.name}> {section.name}</p>
              </DelayLink>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Aside;
