import { createSlice } from "@reduxjs/toolkit";

let initState = {
  selected: "",
  transition: false,
  routeChange: false,
};

const etcSlice = createSlice({
  name: "etc",
  initialState: initState,
  reducers: {
    setSection(state, action) {
      state.selected = action.payload;
    },
    setTransition(state) {
      state.transition = !state.transition;
    },
    setRouteChange(state, action) {
      state.routeChange = action.payload;
      console.log(state.routeChange);
    },
  },
});

export const { setSection, setTransition, setRouteChange } = etcSlice.actions;

export default etcSlice.reducer;
