import React, { useEffect } from "react";
import { Route, useLocation } from "react-router-dom";
import { useDispatch } from "react-redux";

import "./main.styles.scss";
import contents from "../../assets/services-content.data";

import WelcomeComp from "../../components/sections/welcome/welcome.component";
import ServicesComp from "../../components/sections/services/services.component";
import ProjectsComp from "../../components/sections/projects/projects.component";
import AboutComp from "../../components/sections/about/about.component";
import Aside from "../../components/aside/aside.component";
import Slide from "../../components/slide/slide.component";
import sections from "../../assets/sections.data";
import { selectSection } from "../../redux/menu/menu.reducer";

const MainComponent = () => {
  const dispatch = useDispatch();
  const location = useLocation();

  useEffect(() => {
    dispatch(selectSection(location.pathname));
  }, [dispatch, location]);

  return (
    <div className="main-page">
      <Slide />
      <Aside sections={sections} />
      <Route path="/" component={WelcomeComp} exact />
      <Route path="/services">
        <ServicesComp contents={contents} />
      </Route>
      <Route path="/projects" component={ProjectsComp} />
      <Route path="/about" component={AboutComp} />
    </div>
  );
};

export default MainComponent;
