import React from "react";

import logo from "../../assets/logo.svg";
import "./slide.styles.scss";

const Slide = () => {
  return (
    <div className="slide">
      <div className="logo">
        <div className="text">
          <p>Alejandro Camarena</p>
        </div>
        <img src={logo} alt="logo" />
      </div>
    </div>
  );
};

export default Slide;
