import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "./about.styles.scss";

import LogoComp from "../../logo/logo.component";
import ThreeScene from "../../three-js/scene.component";
import Myform from "./contact.form.component";

const AboutComp = () => {
  return (
    <div className="about-sub-comp">
      <ThreeScene />
      <LogoComp />
      <div className="panels">
        <div className="info">
          <div className="title">
            <p>Find new ideas</p>
          </div>
          <div className="desc">
            <p>
              I'm a developer focus on dead lines and client satisfaccion via
              constant feedback and helping to make their ideas real.{" "}
            </p>
          </div>
        </div>

        <div className="contact-form">
          <div className="form-wrapper">
            <Myform />
            <p>
              send your consult and I'll be happy to answer as soon as posible{" "}
            </p>
          </div>
          <div className="social-contact">
            <span className="line"></span>
            <a
              href="https://twitter.com/AlexCamarena95"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FontAwesomeIcon icon={["fab", "twitter"]} />
            </a>
            <a
              href="https://codepen.io/AlexCamarena"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FontAwesomeIcon icon={["fab", "codepen"]} />
            </a>
            <a
              href="https://www.freelancer.com/u/EricHaller"
              target="_blank"
              rel="noopener noreferrer"
            >
              <FontAwesomeIcon icon="handshake" />
            </a>
            <p>follow</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AboutComp;
