import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";

import "./services.styles.scss";

import { useWindowEvent } from "../../../tools/tools";
import { setRouteChange } from "../../../redux/etc/etc.reducer";

const ServicesComp = ({ contents }) => {
  const history = useHistory();
  const { routeChange } = useSelector((selector) => selector.etc);
  const dispatch = useDispatch();

  useWindowEvent("wheel", (e) => {
    const container = document.querySelector(".serv-sub-comp");

    if (Math.sign(e.deltaY) === -1 && window.scrollY === 0) {
      if (routeChange) return;
      dispatch(setRouteChange(true));
      setTimeout(() => {
        history.push("/");
        dispatch(setRouteChange(false));
      }, 2000);
    } else if (
      Math.sign(e.deltaY) === 1 &&
      window.innerHeight + window.scrollY >= document.body.offsetHeight
    ) {
      if (routeChange) return;
      dispatch(setRouteChange(true));
      setTimeout(() => {
        history.push("/projects");
        dispatch(setRouteChange(false));
      }, 2000);
    }
  });

  useEffect(() => {
    document.querySelector("body").style.overflowY = "visible";
    document.querySelector("body").style.overflowX = "hidden";
    dispatch(setRouteChange(false));
  }, []);

  return (
    <div className={routeChange ? "serv-sub-comp active" : "serv-sub-comp"}>
      <div className="wire"></div>
      <div className="background"></div>
      <div className="title">
        <div className="title-circle">
          <svg viewBox="-30 -20 150 150" width="100%" height="100%">
            <path id="curve" d="M10,50a40,40 0 1,0 80,0a40,40 0 1,0 -80,0">
              <animateTransform
                attributeName="transform"
                begin="0s"
                dur="30s"
                type="rotate"
                from="0 50 50"
                to="360 50 50"
                repeatCount="indefinite"
              ></animateTransform>
            </path>
            <text dy="15">
              <textPath xlinkHref="#curve">full stack developer</textPath>
            </text>
          </svg>
        </div>
        <div className="description">
          Best tools to give your project a boost
        </div>
      </div>
      <div className="panels" data-scrollbar>
        {contents.map((cont, id) => (
          <div className={cont.name} key={id}>
            <div className="title-wrap">
              <div className="title">{cont.name}</div>
              <div className="description">{cont.desc}</div>
            </div>
            <div className="filter">
              <div className="slider">
                <div className="slider-one">
                  <div>{cont.sliderOne}</div>
                </div>
                <div className="slider-two">
                  <div>{cont.sliderTwo}</div>
                </div>
                <div className="slider-three">
                  <div>{cont.sliderThree}</div>
                </div>
              </div>
            </div>
            <div
              className="background"
              style={{ backgroundImage: `url(${cont.background})` }}
            ></div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ServicesComp;
